'use client'
import React, { useState } from 'react';
import NotificationCard from '@/components/content/notifications';

interface Notification {
  id: number;
  title: string;
  message: string;
  type: string;
  read: boolean;
}

const NotificationPage: React.FC = () => {
  const [notifications, setNotifications] = useState<Notification[]>([
    {
      id: 1,
      title: 'Welcome!',
      message: 'Thank you for joining us.',
      type: 'Welcome',
      read: false,
    },
    {
      id: 2,
      title: 'Maintenance Alert',
      message: 'Scheduled maintenance on Jan 8th.',
      type: 'Maintenance',
      read: false,
    },
    {
      id: 3,
      title: 'Feature Update',
      message: 'New dark mode feature is live.',
      type: 'Update',
      read: true,
    },
  ]);
  const [searchQuery, setSearchQuery] = useState('');
  const [filterType, setFilterType] = useState('');

  const handleMarkAsRead = (id: number) => {
    setNotifications((prev) =>
      prev.map((notif) =>
        notif.id === id ? { ...notif, read: true } : notif
      )
    );
  };

  const handleDelete = (id: number) => {
    setNotifications((prev) => prev.filter((notif) => notif.id !== id));
  };

  const handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchQuery(e.target.value);
  };

  const handleFilterChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    setFilterType(e.target.value);
  };

  const filteredNotifications = notifications.filter((notif) => {
    const matchesSearch =
      notif.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
      notif.message.toLowerCase().includes(searchQuery.toLowerCase());
    const matchesFilter = filterType ? notif.type === filterType : true;
    return matchesSearch && matchesFilter;
  });

  return (
    <div className="max-w-3xl mx-auto p-4">
      <h1 className="text-2xl font-bold mb-4">Notifications</h1>
      <div className="flex items-center space-x-4 mb-4">
        <input
          type="text"
          placeholder="Search notifications..."
          value={searchQuery}
          onChange={handleSearch}
          className="p-2 border border-gray-300 rounded-md w-full focus:ring-2 focus:ring-purple-500"
        />
        <select
          value={filterType}
          onChange={handleFilterChange}
          className="p-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-purple-500"
        >
          <option value="">All Types</option>
          <option value="Welcome">Welcome</option>
          <option value="Maintenance">Maintenance</option>
          <option value="Update">Update</option>
        </select>
      </div>
      <ul className="space-y-4">
        {filteredNotifications.length > 0 ? (
          filteredNotifications.map((notification) => (
            <NotificationCard
              key={notification.id}
              id={notification.id}
              title={notification.title}
              message={notification.message}
              type={notification.type}
              read={notification.read}
              onMarkAsRead={handleMarkAsRead}
              onDelete={handleDelete}
            />
          ))
        ) : (
          <p className="text-gray-500">No notifications found.</p>
        )}
      </ul>
    </div>
  );
};

export default NotificationPage;
