import React from 'react';

interface NotificationCardProps {
  id: number;
  title: string;
  message: string;
  type: string;
  read: boolean;
  onMarkAsRead: (id: number) => void;
  onDelete: (id: number) => void;
}

const Notifications: React.FC<NotificationCardProps> = ({
  id,
  title,
  message,
  type,
  read,
  onMarkAsRead,
  onDelete,
}) => {
  return (
    <li
      className={`p-4 rounded-lg shadow-md flex justify-between items-start ${
        read ? 'bg-gray-100' : 'bg-purple-100'
      } hover:bg-gray-200 transition`}
    >
      <div>
        <h2 className="font-semibold text-lg">{title}</h2>
        <p className="text-sm text-gray-700">{message}</p>
        <span
          className={`text-xs font-semibold mt-2 inline-block px-2 py-1 rounded ${
            type === 'Update'
              ? 'bg-blue-100 text-blue-700'
              : type === 'Maintenance'
              ? 'bg-yellow-100 text-yellow-700'
              : type === 'Message'
              ? 'bg-green-100 text-green-700'
              : type === 'Welcome'
              ? 'bg-purple-100 text-purple-700'
              : 'bg-orange-100 text-orange-700'
          }`}
        >
          {type}
        </span>
      </div>
      <div className="flex items-center space-x-4">
        {!read && (
          <button
            onClick={() => onMarkAsRead(id)}
            className="text-sm text-blue-600 underline"
          >
            Mark as Read
          </button>
        )}
        <button
          onClick={() => onDelete(id)}
          className="text-sm text-red-600 underline"
        >
          Delete
        </button>
      </div>
    </li>
  );
};

export default Notifications;
